@media only screen and (max-width: 640px) { .content-ibc .use__recommendation__block p { font-size: 24px; font-weight: 400; } }

.content-ibc .use__recommendation__block p:first-child { background: #f64869; padding: 15px 23px; color: #fff; margin-bottom: 30px; margin-right: 25px; line-height: 1.5; letter-spacing: 2.4px; }

@media only screen and (max-width: 640px) { .content-ibc .use__recommendation__block p:last-child { display: block; } }

.content-ibc .use__recommendation__block--2 { display: flex; flex-wrap: wrap; align-items: center; }

.content-ibc .use__recommendation__block--2 p:first-child { color: #2d6965 !important; border-color: #2d6965 !important; }

.content-ibc .use__recommendation__block--2 p:last-child { width: 70%; }

.content-ibc .use__recommendation__block--2 p:last-child span { color: #2d6965; padding-left: 5px; }

@media only screen and (max-width: 640px) { .content-ibc .use__recommendation__block--2 p:last-child span { display: block; padding-left: 0; } }

@media only screen and (max-width: 640px) { .content-ibc .use__recommendation__block--2 p:last-child { width: 100%; } }

.content-ibc .use__recommendation__block--last { position: relative; z-index: 5; }

.content-ibc .use__recommendation__block--last p { color: #000; }

.content-ibc .use__recommendation__block--last p:first-child { border: 1px dashed #f64869; padding: 15px 23px; color: #f64869; margin-bottom: 0; margin-right: 25px; background: none; }

@media only screen and (max-width: 640px) { .content-ibc .use__recommendation__block--last p:first-child { margin-bottom: 20px; } }

.conclusion__consist { display: none; position: fixed; overflow-y: scroll; z-index: 10000; top: 0; bottom: 0; background: #ffffff; height: 100%; width: 100%; padding-top: 120px; padding-bottom: 120px; }

.conclusion__consist .btn-close { position: fixed; right: 45px; top: 45px; height: 40px; width: 40px; cursor: pointer; transition: all 0.3s; }

.conclusion__consist .btn-close:hover span { background: #666666; }

.conclusion__consist .btn-close:hover span:before { background: #666666; }

.conclusion__consist .btn-close span { position: absolute; top: 17px; left: -5px; height: 5px; width: 50px; background: #2e6b67; transform: rotate(45deg); display: block; transition: all 0.3s; }

.conclusion__consist .btn-close span:before { content: ''; height: 5px; width: 50px; background: #2e6b67; transform: rotate(-90deg); position: absolute; transition: all 0.3s; }

.conclusion__consist .head__top__logo { left: 80px; }

.conclusion__consist__block h2 { font-size: 24px; font-weight: 500; letter-spacing: 1.8px; color: #000000; padding-bottom: 10px; }

.conclusion__consist__block h4 { font-size: 18px; font-weight: 400; line-height: 1.33; color: #333333; padding: 30px 0; border-bottom: 2px solid #cccccc; position: relative; }

.conclusion__consist__block h4 span { content: ''; position: absolute; top: 26px; right: 0px; height: 30px; width: 30px; display: block; transition: all 0.3s; }

.conclusion__consist__block h4 span:hover:before { background: #666666; }

.conclusion__consist__block h4 span:hover:after { background: #666666; }

.conclusion__consist__block h4 span:before { content: ''; height: 4px; width: 30px; position: absolute; top: 13px; right: 0px; background: #2e6b67; position: absolute; transition: all 0.3s; }

.conclusion__consist__block h4 span:after { content: ''; height: 4px; width: 30px; position: absolute; top: 13px; right: 0px; background: #2e6b67; transform: rotate(-90deg); position: absolute; transition: all 0.3s; }

.conclusion__consist__block h4.active span:after { opacity: 0; }

.conclusion__consist__block h4 .conclusion__hidden { display: none; }

.conclusion__consist__block h4 p { font-size: 18px; font-weight: 400; line-height: 1.33; color: #666666; padding-top: 30px; }

.conclusion__consist__block h4 p:last-child { padding-bottom: 0; }

.conclusion__consist ul { margin-top: 30px; }

.conclusion__consist ul li { font-size: 24px; padding-left: 50px; position: relative; margin-bottom: 20px; }

.conclusion__consist ul li:before { content: ''; width: 30px; height: 30px; position: absolute; top: 50%; transform: translateY(-50%); left: 0; background: url("../img/afc-landing-svg/checkmark-green.svg") no-repeat; padding: 10px; }

.pop-up { display: none; position: fixed; overflow-y: scroll; z-index: 999; top: 0; bottom: 0; background: #e8e8e8; height: 100%; width: 100%; }

.pop-up .btn-close { top: 20px; right: 45px; }

.pop-up .row { height: initial; }

.pop-up .head__top__logo { left: 80px; }

.pop-up__block { position: absolute; left: 50%; top: 50%; transform: translate(-50%, -50%); background: url("../img/pop-up__bg.png") no-repeat; background-size: cover; height: 600px; width: 720px; box-sizing: border-box; display: flex; justify-content: center; align-items: center; }

.pop-up__block__cont { position: relative; top: -30px; }

.pop-up__block h2 { font-size: 24px; color: #000; padding: 0 40px 30px; }

.pop-up__block h2:nth-child(2) { color: #2e6b67; padding: 0; padding-bottom: 30px; }

.pop-up__block p { color: #2e6b67; font-size: 18px; padding-bottom: 60px; }

.pop-up__block .btn__left { padding: 20px 25px; background: #2e6b67; margin-right: 20px; border: 2px solid #2e6b67; color: #ffffff; text-decoration: none; text-transform: uppercase; transition: all 0.3s; }

.pop-up__block .btn__left:hover { background: none; color: #2e6b67; }

.pop-up__block .btn__right { padding: 20px 30px; margin-right: 20px; color: #2d6965; border: 2px solid #2d6965; text-decoration: none; text-transform: uppercase; transition: all 0.3s; }

.pop-up__block .btn__right:hover { color: #000000; border-color: #000; }

.pop-up .form-game p { font-size: 24px; color: #000; padding-bottom: 30px; }

.pop-up .form-game input { width: 90%; box-sizing: border-box; padding: 15px 25px; border: 2px solid #e8e8e8; margin-bottom: 20px; color: #999999; font-size: 16px; opacity: 1; font-family: 'Proxima Nova'; }

.pop-up .form-game .submit { color: #ffffff; background: #2e6b67; border: 2px solid #2e6b67; margin-top: 10px; transition: all 0.3s; letter-spacing: 3px; font-weight: 600; font-family: 'Proxima Nova'; }

.pop-up .form-game .submit:hover { background: none; color: #2e6b67; }

.pop-up__result { display: none; position: absolute; left: 50%; top: 50%; transform: translate(-50%, -50%); }

.pop-up__result p { font-size: 18px; color: #000; padding-bottom: 30px; }

.pop-up__result .submit__home { width: 65%; box-sizing: border-box; padding: 15px 25px; margin-bottom: 20px; font-size: 16px; color: #ffffff; background: #2e6b67; border: 2px solid #2e6b67; margin-top: 10px; transition: all 0.3s; }

.pop-up__result .submit__home:hover { background: none; color: #2e6b67; }

.pop-up .thanks { font-weight: 600; margin-top: 50px; padding-bottom: 40px; color: #000000; }

.pop-up .thanks--2 { padding-bottom: 20px; }

.pop-up .submit__home { width: 65%; box-sizing: border-box; padding: 15px 25px; margin-bottom: 20px; font-size: 16px; color: #ffffff; background: #2e6b67; border: 2px solid #2e6b67; margin-top: 10px; transition: all 0.3s; text-decoration: none; letter-spacing: 3px; font-weight: 600; display: block; position: relative; left: 50%; transform: translateX(-50%); font-family: 'Proxima Nova'; }

.pop-up .submit__home:hover { background: none; color: #2e6b67; }

.pop-up .contact__block__social { justify-content: center; }

.pop-up .contact__block__social__block { margin-right: 33px; margin-bottom: 20px; }

.pop-up-checkout h2 { text-transform: none; padding-top: 30px; padding-bottom: 0; }
