.step-3 {
	margin-top: 100px;
	@include r(640) {
		margin-top: 0px;
		.container {
			padding: 0 80px; } }
	.step {
		&__delivery {
			display: flex;
			flex-wrap: wrap;
			margin-bottom: 70px;
			@include r(640) {
				margin-top: 20px; }
			h3 {
				@include r(640) {
					font-size: 30px;
					letter-spacing: 1.5px;
					text-transform: uppercase;
					text-align: center;
					margin-bottom: 50px;
					span {
						font-weight: 300; } } }
			h2 {
				width: 100%;
				margin-bottom: 40px;
				@include r(640) {
					font-weight: 600;
					font-size: 24px;
					text-transform: none; } }
			label {
				border: 1px dashed $dark-grey-blue-two;
				width: 370px;
				padding: 20px;
				display: flex;
				justify-content: center;
				align-items: center;
				cursor: pointer;
				margin-right: 30px;
				font-weight: 600;
				font-size: 14px;
				position: relative;
				h4 {
					margin-bottom: 5px; }
				p {
					margin-bottom: 5px;
					span {
						padding-right: 5px; } }
				@include r(640) {
					h4 {
						font-size: 24px;
						font-weight: 600;
						margin-bottom: 10px;
						br {
							display: none; } }
					p {
						margin-bottom: 10px;
						span {
							padding-right: 5px; } } }
				@include r(640) {
					width: 100%;
					margin-bottom: 15px;
					font-size: 24px;
					font-weight: 500;
					border: none;
					flex-direction: column;
					height: 100%;
					padding: 50px;
					padding-left: 100px;
					align-items: start;
					.step__delivery__check {
						width: 40px;
						height: 40px;
						border-radius: 50%;
						border: 1px solid $dark-grey-blue-two;
						position: absolute;
						left: 20px; } }
				.icon {
					width: 200px;
					height: 100px;
					margin-right: 30px;
					@include r(640) {
						display: none; } } }
			.delivery-1 {
				label {
					.step__delivery__check {
						top: 60px; } } }
			.delivery-3 {
				label {
					.step__delivery__check {
						top: 46px; }
					.icon {
						width: 70px;
						height: 70px;
						margin-right: 40px; } } }
			input {
				display: none;
				&:checked + label {
					background: #e8e8e8;
					.step__delivery__check {
						background: $dark-grey-blue-two; } } } }
		&__payment {
			margin-bottom: 150px;
			@include r(640) {
				margin-bottom: 50px; }
			h2 {
				margin-bottom: 50px;
				@include r(640) {
					font-weight: 600;
					font-size: 24px;
					text-transform: none; } }
			&__block {
				display: flex;
				margin-bottom: 50px;
				@include r(640) {
					margin-bottom: 15px; }
				&:nth-of-type(2) {
					margin-bottom: 30px;
					@include r(640) {
						margin-bottom: 15px; } } }
			.label {
				top: 2px;
				@include r(640) {
					display: none; } }
			@include r(640) {
				.label-for {
					background: #fff;
					padding: 30px;
					width: 100%;
					border: 1px dashed $dark-grey-blue-two;
					left: 0;
					h3 {
						font-size: 24px;
						margin-bottom: 0;
						font-weight: 600; }
					p {
						margin-top: 20px;
						font-size: 18px; }
					img {
						width: 75px; } }
				input:checked {
					+ .label {
						+ .label-for {
								background: #e8e8e8;
								border: 1px solid $dark-grey-blue-two;
								h3, p {
									color: #000; } } } } } } } }

