.order {
	background: url('../img/afc-landing/order-bg.png') no-repeat;
	background-size: cover;
	padding-bottom: 110px;
	position: relative;
	z-index: 1;
	@include r(640) {
		background: url('../img/afc-landing/order-mobile-bg.png') no-repeat;
		background-size: auto 100%; }
	h2 {
		padding-top: 115px;
		color: #fff;
		text-transform: uppercase;
		font-size: 30px;
		font-weight: 400;
		letter-spacing: 1.5px; }
	&__block {
		position: relative;
		padding: 40px 40px 70px;
		@include r(640) {
			padding-bottom: 440px; }
		&:before {
			content: '';
			background: url('../img/afc-landing-svg/order.svg') no-repeat;
			width: 300px;
			height: 120px;
			position: absolute;
			left: -30px;
			@include r(640) {
				left: 50%;
				transform: translateX(-50%); } }
		span {
			font-size: 35px;
			color: #f64869;
			font-weight: 400;
			letter-spacing: 4.4px;
			display: inline-block;
			padding-top: 18px; }
		p {
			color: #f64869;
			font-size: 21px;
			letter-spacing: 1.1px; } }
	.btn {
		background: #f64869;
		border: 2px solid #f64869;
		@include r(640) {
			padding: 25px 130px;
			position: relative;
			top: 50px;
			font-size: 24px; }
		&:hover {
			background: none;
			color: #f64869; } } }
.footer {
	background: #e8e8e8;
	position: relative;
	padding-top: 20px;
	@include r(640) {
		height: 100px;
		.container {
			position: relative; } }
	&:after {
		content: '';
		background: url('../img/afc-landing-svg/consist-bg.svg') no-repeat;
		background-size: cover;
		width: 101%;
		height: 60px;
		position: absolute;
		left: 0;
		top: -10px;
		z-index: 2;
		@include r(640) {
			width: 100%; } }
	p {
		color: #666666;
		font-size: 16px;
		position: relative;
		z-index: 4;
		padding-bottom: 10px;
		@include r(640) {
			font-size: 24px; } }
	a {
		position: relative;
		display: block;
		z-index: 4;
		img {
			width: 100px;
			@include r(640) {
				width: 150px; } } } }





