.home {
  background-image: linear-gradient(149deg, #86c6af, #f3efdf);
  @include r(640) {
    height: 1150px; }
  &__comp {
    width: 1100px;
    height: 400px;
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%);
    img {
      width: 100%; }
    &__scene {
      position: absolute; } }
  &__comp--2 {
    width: 1100px;
    height: 400px;
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%);
    @include r(640) {
      left: 56%; }
    &__scene {
      position: absolute;
      img {
        width: 100%; } }
    &__photo {
      width: 450px;
      position: relative;
      left: 45%;
      transform: translateX(-50%);
      img {
        width: 100%; } } }
  &__block {
    margin-left: 30px;
    text-align: left;
    position: relative;
    z-index: 10;
    height: 250px;
    @include r(640) {
      margin-left: 0;
      text-align: center;
      top: 370px; }
    p {
      font-size: 24px;
      font-weight: 600;
      line-height: 1.5;
      letter-spacing: 1.8px;
      text-transform: uppercase;
      color: $dark-grey-blue-two;
      margin-bottom: 20px;
      @include r(640) {
        font-size: 30px;
        letter-spacing: 2.2px;
        line-height: 1.33; } }
    span {
      color: $dark-grey-blue-two;
      font-weight: 600;
      font-size: 16px;
      @include r(640) {
        font-size: 24px; } } }
  .swiper-container, .swiper-wrapper, .swiper-slide {
    width: 100%;
    height: 100%;
    position: relative;
    a {
      position: relative;
      font-size: 18px;
      font-weight: 600;
      color: $greeny-blue;
      display: block;
      cursor: pointer;
      letter-spacing: 1.8px;
      text-transform: uppercase;
      transition: all 0.3s;
      z-index: 10;
      top: 40px;
      @include r(640) {
        font-size: 24px;
        letter-spacing: 2.4px;
        text-align: left;
        display: flex;
        justify-content: center;
        left: 60px; }
      &:hover {
        color: $dark-grey-blue-two; }
      span {
        position: absolute;
        top: -15px;
        left: -100px;
        width: 75px;
        height: 75px;
        border-radius: 50%;
        padding: 5px;
        background-image: linear-gradient(to right, #32d6c1 37%, #284a41);
        transition: all 0.3s;
        background-size: 200% 200%;
        animation: Gradient 5s ease infinite;
        @include r(640) {
          width: 90px;
          height: 90px;
          left: 60px; } } } }
  .swiper-slide-prev {
    opacity: 0 !important; }
  .swiper-pagination {
    text-align: left;
    position: absolute;
    top: -50px;
    left: inherit;
    width: initial;
    margin-bottom: 50px;
    @include r(640) {
      top: -170px;
      right: 0px; }
    &-bullet {
      border: solid 1px $white;
      width: 40px;
      height: 10px;
      display: inline-block;
      margin-right: 15px;
      border-radius: 0;
      background: transparent;
      opacity: 1;
      @include r(640) {
        width: 50px;
        height: 15px; }
      &-active {
        background: #fff; } } } }


