.contact {
  background: #e8e8e8;
  position: relative;
  overflow: hidden;
  @include r(640) {
    height: 900px;
    overflow: visible; }
  @include r(640) {
    &:before {
      content: '';
      background: url('/img/separator-m.svg') no-repeat;
      width: 100%;
      height: 112px;
      top: -15px;
      position: absolute;
      transform: scale(-1, 1); }
    &:after {
      content: '';
      background: url('/img/separator-m.svg') no-repeat;
      width: 100%;
      height: 112px;
      bottom: -15px;
      position: absolute;
      transform: scale(1, -1); } }
  &__left {
    bottom: 0px;
    left: 4%;
    height: 400px;
    width: 500px;
    position: absolute;
    .img {
      position: absolute;
      bottom: 0;
      left: 0; } }
  &__right {
    top: -45px;
    right: 0;
    position: absolute;
    width: 400px; }
  img {
    width: 100%; }
  h2 {
    margin-bottom: 100px;
    position: relative;
    @include r(640) {
      margin-top: 40px;
      margin-bottom: 40px;
      font-size: 30px; } }
  &__block {
    padding-left: 50px;
    @include r(640) {
      padding-left: 0; }
    h3 {
      font-size: 18px;
      font-weight: 600;
      text-align: left;
      @include r(640) {
        font-size: 30px;
        text-align: center; } }
    h4 {
      font-weight: 500;
      font-size: 16px;
      top: 60px;
      position: relative;
      text-align: left;
      @include r(640) {
        font-size: 24px;
        top: 45px;
        z-index: 10; } }
    span {
      font-size: 18px;
      margin-bottom: 20px;
      display: block;
      text-align: left;
      @include r(640) {
        font-size: 30px; } }
    .tel, .mail {
      font-size: 18px;
      margin-bottom: 20px;
      display: block;
      text-decoration: none;
      text-align: left;
      color: $black-two;
      &:hover {
        text-decoration: underline; }
      @include r(640) {
        font-size: 30px;
        text-align: center; } }
    &__social {
      display: flex;
      flex-wrap: wrap;
      margin-top: 30px;
      @include r(640) {
        justify-content: center; }
      &__block {
        width: 80px;
        height: 80px;
        @include r(640) {
          width: 105px;
          height: 105px; }
        &:not(:last-child) {
          margin-right: 40px; }
        a {
          display: flex;
          justify-content: center;
          align-items: center;
          width: 80px;
          height: 80px;
          border: 2px solid $dark-grey-blue-two;
          transition: all 0.3s;
          @include r(640) {
            width: 105px;
            height: 105px; }
          .icon {
            fill: $dark-grey-blue-two;
            width: 35px;
            height: 35px;
            @include r(640) {
              width: 45px;
              height: 45px; } }
          &:hover {
            background: $dark-grey-blue-two;
            .icon {
              fill: #ffffff;
              transition: all 0.3s; } } }
        p {
          margin-top: 15px;
          text-align: center;
          font-size: 16px;
          color: $dark-grey-blue-two;
          @include r(640) {
            font-size: 24px; } } } }
    .home-form {
      height: 460px; }
    form {
      width: 340px;
      h3 {
        margin-bottom: 0px; }
      input {
        background: transparent;
        display: block;
        border-bottom: 2px solid $warm-grey;
        width: 100%;
        box-sizing: border-box;
        padding: 22px;
        font-size: 16px;
        color: $denim;
        margin-bottom: 10px;
        transition: all 0.3s;
        &:focus {
          border-bottom: 2px solid $dark-grey-blue-two; } }
      input[type="submit"] {
        margin-top: 0px;
        background: #ffffff;
        font-size: 16.1px;
        font-weight: 500;
        letter-spacing: 1.6px;
        border: none;
        color: $dark-grey-blue-two;
        transition: all 0.3s;
        &:hover {
          background: $dark-grey-blue-two;
          color: #ffffff; } }
      #comment {
        margin-bottom: 30px; }
      .label-for {
        position: relative;
        bottom: 18px;
        left: 40px;
        margin-bottom: 0;
        top: inherit;
        @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
          left: 0; } } }
    .response {
      display: none;
      font-size: 18px;
      font-weight: 600;
      height: 100%;
      p {
        position: relative;
        top: 50%;
        transform: translateY(-50%); } } }
  .copyright {
    position: absolute;
    bottom: 0px;
    width: 80px;
    right: 145px;
    img {
      width: 100px; } }
  .social {
    position: relative;
    bottom: -20px;
    display: block;
    svg {
      width: 60px;
      height: 60px;
      fill: $dark-grey-blue-two; } } }
.contact__block__info.hidden-sm {
  margin-top: 25px; }

.scene__contact {
  position: absolute;
  img {
    width: 100%; }
  &--1 {
    top: 10px;
    left: 210px;
    z-index: 10;
    width: 100px; }
  &--2 {
    left: 370px;
    bottom: 40px;
    width: 150px; }
  &--3 {
    width: 100px;
    top: 30px;
    left: -90px; }
  &--4 {
    width: 80px;
    top: 120px;
    left: -90px; }
  &--5 {
    right: 140px;
    bottom: -280px;
    width: 125px; }
  &--6 {
    right: 50px;
    width: 60px; }
  &--7 {
    width: 90px;
    bottom: -100px;
    left: 45px; }
  &--1--m {
    width: 350px;
    left: -80px;
    top: -120px; }
  &--2--m {
    width: 480px;
    top: -120px;
    right: -190px; } }



.contact__form {
  background: #e8e8e8;
  width: 100%;
  position: absolute;
  height: 100%;
  z-index: 100;
  top: 0;
  padding: 100px 0;
  overflow: scroll;
  display: none;
  form {
    width: 480px;
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    overflow: hidden; }
  h3 {
    font-size: 30px;
    font-weight: 600;
    text-align: center;
    margin-bottom: 50px;
    color: $black; }
  .form-element {
    width: 100%;
    input {
      background: transparent;
      width: 100%;
      height: 80px;
      font-size: 24px; }
    label {
      font-size: 24px; } }
  textarea {
    background: transparent;
    font-size: 24px;
    width: 100%;
    height: 235px;
    border: 2px solid #999;
    color: $dark-grey-blue-two;
    border-radius: 0; }
  input[type=submit] {
    margin-top: 20px;
    background: $dark-grey-blue-two;
    width: 100%;
    height: 80px;
    font-size: 24px;
    color: #ffffff;
    letter-spacing: 2.4px; } }

.contact footer.footer {
  background: #ffffff;
  position: static;
  font-size: 24px;
  a {
    position: relative;
    top: 5px; }
  img {
    width: 113px;
    @include r(640) {
      width: 150px; } }
  &:after {
    content: none; } }
