.afc {
	background: #e8e8e8;
	position: relative;
	@include r(640) {
		height: 1150px;
		.container {
			overflow: hidden; } }
	@include r(640) {
		&:before {
			content: '';
			background: url('/img/separator-m.svg') no-repeat;
			width: 100%;
			height: 112px;
			bottom: -15px;
			position: absolute;
			transform: scale(-1, 1) rotate(180deg); } }
	&__slider {
		position: relative;
		bottom: 40px;
		@include r(640) {
			top: 10px;
			bottom: inherit;
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center; }
		&__info {
			width: 250px;
			position: absolute;
			top: 60px;
			left: 60px; }
		&--img {
			width: 430px;
			height: 456px;
			z-index: 10;
			position: relative;
			img {
				width: 100%; } }
		&--line {
			position: absolute;
			top: -25px;
			right: -140px;
			width: 265px;
			@include r(640) {
				right: 36px;
				top: 85px;
				width: 340px;
				height: 673px;
				z-index: 100;
				transform: rotate(44deg); }
			img {
				width: 100%; } }
		&--active {
			position: absolute;
			top: 49.5%;
			transform: translateY(-50%);
			right: -4px;
			width: 13px;
			height: 13px;
			border-radius: 50%;
			background-image: linear-gradient(to right, #29413b, #38c9c9);
			@include r(640) {
				top: 81%;
				right: 67px;
				width: 22px;
				height: 22px; }
			&:before {
				content: '';
				width: 23px;
				height: 23px;
				display: block;
				border-radius: 50%;
				border: 1px solid #2f6a66;
				top: -5px;
				left: -5px;
				position: absolute;
				@include r(640) {
					content: none; } } }
		&--prev, &--next {
			position: absolute;
			width: 40px;
			z-index: 100;
			background: transparent;
			margin-top: 0;
			height: 40px;
			background: url('/img/afc/afc-arrow.svg') no-repeat;
			@include r(640) {
				width: 80px;
				height: 80px;
				transition: initial; }
			&:hover {
				background: url('/img/afc/afc-arrow-hover.svg') no-repeat; } }
		&--prev {
			top: 29%;
			right: -1px;
			left: inherit;
			transform: rotate(-14deg);
			@include r(640) {
				right: -34px;
				top: 44%; } }
		&--next {
			top: 63%;
			right: -1px;
			transform: rotate(200deg);
			@include r(640) {
				right: 190px;
				top: 90%;
				transform: rotate(250deg); } }
		.swiper-button-disabled {
			opacity: 1;
			pointer-events: inherit;
			cursor: pointer; } }
	&-slider {
		height: 300px;
		width: 500px;
		transition: all 1s;
		@include r(640) {
			margin-top: 100px; } }
	.swiper-slide {
		font-style: italic;
		font-size: 16px;
		color: $warm-grey;
		transform: translateX(0px);
		transition: all 1s;
		@include r(640) {
			color: $dark-grey-blue-two;
			font-size: 24px;
			display: flex;
			justify-content: center; }
		&-prev, &-active, &-duplicate, &-duplicate-active, &-duplicate-next, &-duplicate-prev {
			transition: all 1s; }
		&-next {
			transform: translateX(30px);
			color: $dark-grey-blue-two;
			transition: all 1s; }
		p {
			height: 100%;
			display: flex;
			align-items: center;
			@include r(640) {
				text-align: center; } } }
	h2 {
		font-size: 24px;
		font-weight: 600;
		letter-spacing: 1.8px;
		text-transform: uppercase;
		margin-left: 40px;
		@include r(640) {
			font-size: 30px;
			text-align: center;
			margin-left: 0; }
		span {
			margin-top: 10px;
			font-size: 16px;
			text-transform: none;
			display: block;
			letter-spacing: 1.2px;
			@include r(640) {
				font-size: 18px;
				text-align: center;
				letter-spacing: 1.4px; } } }
	&__info {
		width: 390px;
		margin-bottom: 50px;
		img {
			width: 100%; } }
	&__price {
		display: flex;
		position: relative;
		@include r(640) {
			flex-direction: row-reverse;
			.btn {
				color: $dark-grey-blue-two;
				border: 1px solid $dark-grey-blue-two;
				width: 310px;
				font-size: 24px;
				padding: 25px 40px; }
			.btn--white {
				margin-right: 0;
				margin-left: 18px;
				background: $dark-grey-blue-two;
				color: #ffffff;
				border: 1px solid $dark-grey-blue-two;
				width: 230px; } }
		&__block {
			margin-right: 15px;
			@include r(640) {
				position: absolute;
				top: -70px; }
			p {
				font-size: 14px;
				color: $black-two;
				@include r(640) {
					display: none; } }
			h3 {
				color: $dark-grey-blue-two;
				font-size: 16px;
				font-weight: 500;
				@include r(640) {
					width: 192px;
					font-size: 24px;
					font-weight: 600; }
				span {
					font-size: 36px;
					margin-left: 2px;
					@include r(640) {
						font-size: 48px;
						font-weight: 600; } }
				.ruble {
					font-weight: 500;
					font-size: 34px;
					@include r(640) {
						font-size: 44px;
						font-weight: 300 !important; } } } } }
	&__bottom {
		position: absolute;
		bottom: 0;
		width: 100%;
		background: url('/img/afc/afc-bottom.svg') no-repeat;
		height: 110px;
		background-size: cover;
		box-sizing: border-box;
		&__block {
			position: relative;
			top: 60px;
			p {
				font-size: 14px;
				font-weight: 600;
				letter-spacing: 0.7px;
				color: $black;
				text-transform: uppercase;
				display: inline-block;
				margin-right: 50px;
				span {
					color: #ffffff;
					padding-left: 5px; } } } } }

