.content-ibc {
	overflow-x: hidden;
	.head {
		padding-bottom: 260px;
		@include r(640) {
			padding-bottom: 20px; }
		img.head__bg {
			width: 90%;
			position: absolute;
			left: -100px;
			top: 50px;
			z-index: 1;
			@include r(640) {
				width: 190%;
				top: -73px;
				left: -320px;
				position: relative; } }
		&__top {
			position: relative;
			&__logo {
				width: 130px;
				height: 170px;
				position: absolute;
				left: 0;
				top: 0;
				z-index: 100;
				padding-top: 25px;
				background: url('/img/logo/logo-bg-green.svg') no-repeat;
				background-size: cover;
				display: flex;
				justify-content: center;
				align-items: center;
				@include r(640) {
					left: 50%;
					transform: translateX(-50%); }
				img {
					width: 80px;
					height: 90px; } }
			&__back {
				position: relative;
				z-index: 4;
				color: $brownish-grey;
				text-decoration: none;
				text-transform: uppercase;
				font-size: 12px;
				font-weight: 600;
				cursor: pointer;
				top: 240px;
				left: 20px;
				transition: all 0.3s;
				@include r(640) {
					display: none; }
				svg {
					position: absolute;
					left: -20px;
					transform: rotate(180deg);
					top: 0px;
					fill: $brownish-grey;
					transition: all 0.3s; }
				&:hover {
					color: $dark-grey-blue-two;
					svg {
						fill: $dark-grey-blue-two; } } } }
		&__block {
			position: relative;
			b {
				font-weight: 600; }
			@include r(640) {
				margin-top: -130px; }
			.row {
				height: 100%; }
			&__text {
				position: relative;
				z-index: 4;
				@include r(640) {
					padding: 0 70px; }
				h2 {
					padding-top: 160px;
					padding-bottom: 15px;
					font-size: 24px;
					font-weight: 500;
					@include r(640) {
						padding-top: 0; } }
				span {
					padding-bottom: 35px;
					display: inline-block;
					font-size: 16px;
					font-weight: 400;
					@include r(640) {
						display: none; } }
				p {
					font-weight: 300;
					font-size: 18px;
					color: #333333;
					@include r(640) {
						font-size: 24px; } }
				.btn {
					background: #2e6b67;
					display: inline-block;
					margin-top: 35px;
					border: 2px solid #2e6b67;
					@include r(640) {
						display: none; }
					&--mobile {
						display: none;
						width: 132%;
						padding: 30px 0;
						letter-spacing: 3.6px;
						margin-left: -17%;
						font-size: 24px;
						font-weight: 400;
						.icon {
							fill: #fff;
							margin-left: -10px; }
						span {
							font-size: 36px;
							display: inline-block;
							padding-bottom: 0;
							@include r(640) {
								font-weight: 600; } }
						@include r(640) {
							display: inline-block;
							.ruble {
								font-size: 33px; } } }
					&:hover {
						background: #ffffff;
						color: #2e6b67; } } } } } }


.head__parallax {
	position: absolute;
	top: 0;
	z-index: 3;
	width: 100%;
	height: 100%; }

.content-ibc {
	#head-1 {
		z-index: 6;
		left: 55%;
		top: -37px;
		@include r(640) {
			display: none; } }
	#head-2 {
		z-index: 6;
		left: 70%;
		top: 88%;
		@include r(640) {
			display: none; } }
	#head-3 {
		z-index: 6;
		left: 74%;
		top: 68%;
		@include r(640) {
			display: none; } } }
